import React, {useContext} from 'react'
import {useParams} from 'react-router-dom'
import {MyContext} from '../context/MyContext'

export default function DetailedTask() {
    const {tasks} = useContext(MyContext)
    const {taskname} = useParams()

    let searchTask = tasks.find(item=>item.text === taskname)
    console.log('search item', searchTask)
    return (
        <div>
            <h1>Task: {searchTask && searchTask.taskname}</h1>
            <p>Status: {searchTask && searchTask.done.toString()}</p>
          
        </div>
    )
}
