import React, {useContext} from 'react';
import {MyContext} from '../context/MyContext'

const ToDonesContainer = () => {
  const {todones, UpdateItem, DeleteItem} = useContext(MyContext)
  
  const toDoNesItems = todones.map((el, index) => {
    return (
      <div className="todones-item" key={index}>
        <p>{el.text}</p>
        <div className="actions">
          <button 
          className="btn" 
          onClick={()=>UpdateItem}
          >&#8635;</button>

          <button
          onClick={()=>DeleteItem} 
          className="btn"
          >&#10007;</button>
        </div>
      </div>
    );
  });

  return (
    <div className="todones-container">
      <h3>BACKLOG</h3>
      {todones.length > 0 && toDoNesItems}
    </div>
  );
};

export default ToDonesContainer;
