import React, {useRef, useState, useContext} from 'react';
import {Link} from 'react-router-dom'
import { MyContext } from '../context/MyContext';

const ToDosContainer = () => {
  const {todos, UpdateItem, AddItem} = useContext(MyContext)

  const input = useRef()
  const [disabled, setDisabled] = useState(false)

  const toDoItems = todos.map((el, index) => {
    return (
      <div className="todo-item" key={index}>
        <p> 
          <Link to={`/task/${el.text}` }>{el.text}</Link>
        </p>

        <div className="actions">
          <button className="btn" onClick={()=>UpdateItem}>&#10004;</button>
        </div>
      </div>
    );
  });

  const DeleteAll = () => {
    if (todos.length > 0) {
      todos.length = 0
    }
  }


  const handleSubmit=(e)=>{
    e.preventDefault();
    setDisabled(true)
    if (input.current.value !== '') {
      AddItem(input.current.value)
    }
    input.current.value = '';
    // or inside the <form> you can reset it this way :
    // e.target.reset()
    setTimeout(()=>{
      setDisabled(false)
    }, 1000)
  }


  return (
    <div className="todos-container">
      <form className="todo-form" onSubmit={handleSubmit}>
        <label className="input-item">
          <input type="text" name="todo" ref={input}/>
        </label>
        <input disabled={disabled} className="btn" type="submit" value="ADD" />
        <button className="btn" type="delete" value="DELETE ALL" onClick={DeleteAll}>Delete ALL tasks</button>
      </form>
      <div className="todos">
        <h3>TO DO</h3>
        {todos.length > 0 && toDoItems}
      </div>
    </div>
  );
};

export default ToDosContainer;
