import React, { useState, useEffect } from 'react'
import { MyContext } from './MyContext'
import {v4 as uuid} from 'uuid'


export default function Container(props) {

    const [tasks, setTasks] = useState([])
    const todos = tasks.filter(item => !item.done)
    const todones = tasks.filter(item => item.done)
    
  
    //to execute on load when the application is just opened for the first time:
  // the same as ComponentDidMount
  useEffect(()=>{ 
    let data = localStorage.getItem('todoItems')
    if (data) {
      let parsedData = JSON.parse(data)
      setTasks(parsedData)
    }
  }, [])


  const DeleteItem = (id) => {
    const updated = tasks.filter(task => task.id !== id)
    localStorage.setItem('todoItems', JSON.stringify(updated))
    setTasks(updated)
  }

  const AddItem =(text) => {
    let task = {id: uuid(), text: text, done: false}
    //local storage is permanent
    localStorage.setItem('todoItems', JSON.stringify([task, ...tasks]))

    //session is only for one session until the tab is closed
    // sessionStorage.setItem('todoItems', JSON.stringify([task, ...tasks]))

    setTasks([task, ...tasks])
  }

  const UpdateItem = (id) => {
    let updatedTasks = tasks.map(task => {
      if (task.id === id) {
        // change from false to true and vice versa: 
        task.done = !task.done
      }
      return task;
    })
    localStorage.setItem('todoItems', JSON.stringify([updatedTasks, ...tasks]))

    setTasks(updatedTasks)

 //// SHORTER WAY WITH TERNARY OPERATOR:
    // let updatedTasks = tasks.map(item=> item.id === id ? 
    //   {...item, done:!item.done} : item)
    //   setTasks(updatedTasks)
  }
    return (
        <MyContext.Provider value={{
            tasks, setTasks, todos, todones, DeleteItem, AddItem, UpdateItem}}>
            {props.children}
        </MyContext.Provider>
    )

}